function initCatalog() {
	let isCatalogButtonElementExpanded = false;

	const catalogButtonElement = document.getElementById('header__catalog-button');
	const catalogElement = document.getElementById('page__catalog');

	catalogButtonElement.addEventListener('click', () => {
		isCatalogButtonElementExpanded = !isCatalogButtonElementExpanded;

		catalogButtonElement.setAttribute('aria-expanded', isCatalogButtonElementExpanded);

		if (isCatalogButtonElementExpanded) {
			catalogElement.classList.add('catalog--open');
		} else {
			catalogElement.classList.remove('catalog--open');
		}
	});

	const isCatalogNavButtonElementExpandedMap = new Map();
	const catalogNavButtonElements = document.querySelectorAll('.catalog-nav-button');
	catalogNavButtonElements.forEach((catalogNavButtonElement) => {
		isCatalogNavButtonElementExpandedMap.set(catalogNavButtonElement, false);

		catalogNavButtonElement.addEventListener('click', () => {
			isCatalogNavButtonElementExpandedMap.forEach((val, key, map) => {
				const catalogNavListNestedElement = document.getElementById(key.getAttribute('aria-controls'));

				if (key === catalogNavButtonElement) {
					const isCatalogNavButtonExpanded = !val;

					catalogNavButtonElement.setAttribute('aria-expanded', isCatalogNavButtonExpanded);

					if (isCatalogNavButtonExpanded) {
						catalogNavListNestedElement.classList.add('catalog-nav__list--open');
					} else {
						catalogNavListNestedElement.classList.remove('catalog-nav__list--open');
					}

					map.set(key, isCatalogNavButtonExpanded);
				} else {
					key.setAttribute('aria-expanded', false);
					catalogNavListNestedElement.classList.remove('catalog-nav__list--open');
					map.set(key, false);
				}
			});
		});
	});
}

export default initCatalog;
